<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner2.png" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner2.png" class="img-responsive m-bg" />
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng">
						<a href="http://web.ahmodern.cn/zszt/school_admission_brochure">
							<img src="../../static/img/bg6.png" class="img-responsive" />
						</a>
						<a href="http://web.ahmodern.cn/zszt/register_guide">
							<img src="../../static/img/bg7.png" class="img-responsive" />
						</a>		
					</div>
				</div>
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h4>专业建设</h4>
					<div class="info-title-right pc-bg">
						<span><router-link to="/">首页</router-link></span>
						<span>></span>
						<span><router-link to="/special_construction">专业建设</router-link></span>
					</div>
				</div>
				<div class="news">
					<span class="h3">{{news.title}}</span>
					<div class="news-info">
						<span>时间：{{news.add_time}}</span>
					</div>
					<div class="page" v-html="news.content"></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {	
	data(){
		return{
			news:''
		}
	},
	created:function(){
    	var _self = this;
    	var newsId = this.$route.params.id;
    	document.body.scrollTop = 0;
    },
	beforeRouteEnter (to, from, next) {
		next(vm => {
		    var _self = vm;
			var newsId = vm.$route.params.id;
			console.log(typeof(newsId))
	　　　	var url="http://api.7171.la/api/artdetail";            
		  	vm.$http.post(url,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de',id:newsId}
				).then(function (data) {
	    		var json = data.data;             
		  		_self.news = json;
		  		console.log(json)
		  		_self.$nextTick(() => {
					document.title=_self.news.title+"_安徽现代信息工程职业学院互联网学院";
				});
		  	});
	  	});
	},
    watch: {
        '$route': function () {
            this.newsId = this.$route.params.id;
        }
    },
	activated(){
		 document.documentElement.scrollTop = 0
	}
}		
</script>

<style>
</style>


