			            </div>
			            <div class="swiper-slide">
							<img src="../../static/img/big5.jpg" class="img-responsive"/>			            	
			            </div>
			            <div class="swiper-slide">
							<img src="../../static/img/big6.jpg" class="img-responsive"/>			            	
