			        <div class="swiper-button-next carousel-control g-n"><i class="iconfont icon-right"></i></div>
			        <div class="swiper-button-prev carousel-control g-p"><i class="iconfont icon-back"></i></div>
			    </div>
			    <div class="swiper-container gallery-thumbs">
			        <div class="swiper-wrapper">
			            <div class="swiper-slide">
							<img src="../../static/img/thumb1.png" class="img-responsive"/>			            	
