			            <div class="swiper-slide">
							<img src="../../static/img/thumb2.png" class="img-responsive"/>			            	
			            </div>
			            <div class="swiper-slide">
							<img src="../../static/img/thumb3.png" class="img-responsive"/>			            	
			            </div>
			            <div class="swiper-slide">
							<img src="../../static/img/thumb4.png" class="img-responsive"/>			            	
