	</div>
</template>

<script>
import swiper from 'Swiper';
export default {
	data(){
		return{
			dynList:[],
			noticeList:[],
			empList:[],
			stuList:[],
			teacherList:[],
			lunList:[],
		}
	},
	created(){
		var _self = this;
		var url="http://api.7171.la/api/article";            
		  this.$http.post(url,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de',type:'42,43,44,45'}
			).then(function (data) {
       		var json = data.data;
		  	_self.totalList = json;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "42"){
					if(_self.dynList.length<3){
						_self.dynList.push(_self.totalList[i]);
						var dateTime = _self.totalList[i].add_time;
						dateTime =dateTime.split("-");
						_self.totalList[i].day = dateTime[2];
						_self.totalList[i].mouth = dateTime[1];
						_self.totalList[i].year = dateTime[0];
					}
				}
				if(_self.totalList[i].type == "43"){
					if(_self.noticeList.length<5){
						_self.noticeList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "44"){
					if(_self.empList.length<6){
						_self.empList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "45"){
					if(_self.stuList.length<5){
						_self.stuList.push(_self.totalList[i]);
					}
				}
			}
    	});
		var url2="http://api.7171.la/api/teacher";            
		  this.$http.post(url2,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de'}
			).then(function (data) {
        	_self.teacherList = data.data;
			_self.$nextTick(() => {
				_self.lunbo();				
			});
    	});
	},
	methods: {
	    lunbo(){
		  	var swiper1 = new Swiper('.shizi', {
				effect: 'slide',
                loop: 'true',
                speed: 600,
                autoplay: 5000,
		        nextButton: '.t-n',
    			prevButton: '.t-p',
                
            });
	    },
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	},
	activated(){
		document.documentElement.scrollTop = 0
	},
	mounted(){
		var galleryTop = new Swiper('.gallery-top', {
	        spaceBetween: 10,
	        loop:true,
	        loopedSlides: 3, //looped slides should be the same     
	    });
	    var galleryThumbs = new Swiper('.gallery-thumbs', {
	        spaceBetween: 10,
	        slidesPerView: 4,
	        touchRatio: 0.2,
	        loop:true,
	        loopedSlides: 3, //looped slides should be the same
	    });
	    galleryTop.params.control = galleryThumbs;
	    galleryThumbs.params.control = galleryTop;
	    $('.g-n').click(function(){
	    	event.stopPropagation();
			galleryTop.slideNext();
		})
		$('.g-p').click(function(){
			event.stopPropagation();
			galleryTop.slidePrev();
		})
	    $(".campus-box >div,.thumb").click(function(){
	    	$('.mask').css('visibility','visible');
	    });
	    $(".gallery-thumbs .swiper-slide").click(function(){
	    	event.stopPropagation();
	    	var ind = $(this).index();
	    	galleryTop.slideTo(ind, 1000, false);//切换到第一个slide，速度为1秒
	    });
	    $('.mask').click(function(){
	    	$('.mask').css('visibility','hidden');
	    });
	}
	
}	
</script>

<style>
	.mask{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.8);
		z-index: 2;
		min-width: 320px;
		visibility: hidden;
	}
	.mask > div {
	    position: fixed;
	    top: 10%;
	    left: 5%;
	    width: 90%;
	}
	.mask img{
		display: block;
		margin: 0 auto;
	}
	.gallery-thumbs{
		margin-top: 15px;
	}
	.gallery-thumbs .swiper-slide {
        width: 25%;
        height: 100%;
        opacity: 0.7;
    }
    .gallery-thumbs .swiper-slide-active {
        opacity: 1;
    }
	.shizi .swiper-slide>div{
		margin: 0 auto;
		max-width: 160px;
		position: relative;
	}
	.shizi p{
		position: absolute;
		bottom: 0;
		width: 100%;
		background: rgba(52,90,84,0.8);
		margin: 0;
		padding: 5px 0;
		color: #fff;
	}
	@media only screen and (max-width: 1024px) {
		.mask > div{
			top: 20%;
		}
	}	
	@media only screen and (max-width: 414px) {
		.mask > div{
			top: 25%;
		}
	}	
</style>


