<template>
	<div>
		<div class="nav-mask">	</div>
		<div class="pc-bg">
			<div class="header-top">
				<div class="container">
					<div class="top-left">
						<div>
							<span>您好！欢迎访问安徽现代信息工程职业学院——互联网学院（高职部）</span>
						</div>
					</div>
					<div class="top-right">
						<span>教育部招生代码：145288</span>
						<!--<div>
							<span><i class="iconfont icon-shouye1"></i><a href="javascript:void(0)">招生专题网</a></span>
						</div>
						<div>
							<span>|</span>
						</div>
						<div>
							<span><i class="iconfont icon-xing"></i><a href="javascript:void(0)" @click="addFavorite('http://www.baidu.com','百度')">加入收藏</a></span>
						</div>-->
					</div>
				</div>
			</div>
			<div class="logo">
				<div class="container">
					<img src="../../../static/img/logo.png" class="fl" />
					<img src="../../../static/img/logo2.png" class="fr" />
				</div>
			</div>
			<nav class="pc-nav">
				<div class="container">
					<ul class="list-inline first-nav">
						<li @click="selected(item.name,item.mca)" :class="{active: activeName == item.name || (uri == '/' && index == 0 )}" ref="li" v-for="(item, index) in navList" v-if="index != 7">
							<router-link to="/" class="text-center">{{item.name}}</router-link>
							<ul class="second-nav list-unstyled">
								<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca)" >
									<a class="text-center">{{item1.name}}</a>
								</li>
							</ul>	
						</li>
						<li v-else>
							<a class="text-center" href="http://web.ahmodern.cn/zszt/">{{item.name}}</a>
						</li>
					</ul>
				</div>
			</nav>
		</div>
		<div class="m-bg">
			<div class="m-top">
				<router-link to="/">
					<img src="../../../static/img/m-logo.png" />
				</router-link>
				<img src="../../../static/img/nav.png" class="m-nav-botton" data="close"/>
			</div>
			<div class="m-logo">
				<img src="../../../static/img/m-logo1.png" />
			</div>
			<nav class="m-nav" id="nav">
				<ul>
					<li @click="selected(item.name,item.mca,'mobail',index)"  v-for="(item,index) in navList" >
						<a><i class="iconfont" :class="item.ico"></i><span>{{item.name}}</span></a>
						<ul class="m-second-nav list-unstyled" :class="{active: activeName == item.name }">
							<li v-for="item1 in item._data" @click.stop="selected(item1.name,item1.mca,'child')" ><a><span>{{item1.name}}</span></a></li>
						</ul>
					</li>
				</ul>
			</nav>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
        return{
        	isShow: false,
        	navList: [],
        	gameName:'',
            activeName: '', 
            status: false,
            uri: '',
        }
    },
    created:function(){
    	var _self = this;
    	_self.uri = this.$route.path;
　　　	var url="http://api.7171.la/api/menu";            
	    this.$http.post(url,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de'}
			).then(function (data) {
        	var json = data.data;
		  	_self.navList = json;
		  	_self.navList = _self.navList[0]._data;
		  	_self.$nextTick(() => {
				_self.address(_self.uri);			
			});
    	});      
    },
    watch: {
    	$route (to, from) {
    		this.uri = this.$route.path;
    		this.address(this.uri);
    	}	
    },
	methods: {
	    selected(name,url,type,n) {
	    	$(".first-nav>li").removeClass("active");
	        this.activeName = name;
	        if(name == "招生在线"){
//	      	window.location.href="http://web.sgzzgz.com/zszt"; 
	        }else{
	      		this.$router.push({ path: url });
	        }
	        if((type == "mobail" && n!= 5)||(type == 'child')){ 
	      		$(".m-nav").removeClass("nav-active");
				$(".nav-mask").removeClass("active");
	        }
	        if(type == 'second'){
	        	$(".first-nav>li").find('ul').removeClass('hover');
	        }
	    },
	    address(url){
	    	if( url == "/"){
    			document.title = "安徽现代信息工程职业学院互联网学院";
    			$(".first-nav>li").removeClass("active");
    			$(".first-nav>li").eq(0).addClass("active"); 
    		}
	    	if( url == "/contact"){
    			$(".first-nav>li").removeClass("active");
    			$(".first-nav>li").eq(9).addClass("active"); 
    		}
	    	if( url == "/sc_info"){
    			$(".first-nav>li").removeClass("active");
    			$(".first-nav>li").eq(1).addClass("active"); 
    		}
    		if( url == "/construction" || url.indexOf("news_page2") >= 0){
    			$(".first-nav>li").removeClass("active");
    			$(".first-nav>li").eq(3).addClass("active"); 
    		}
    		if(url.indexOf("news_page1") >= 0 || url == "/sc_dynamics") { 
				$(".first-nav>li").removeClass("active");
			    $(".first-nav>li").eq(2).addClass("active"); 
			}
    		if(url == "/teacher") { 
				$(".first-nav>li").removeClass("active");
			    $(".first-nav>li").eq(4).addClass("active"); 
			}
    		if(url.indexOf("special_major") >= 0 ) {
				$(".first-nav>li").removeClass("active");
			    $(".first-nav>li").eq(5).addClass("active"); 
				}
    		if(url.indexOf("news_page3") >= 0 || url == "/special_construction") { 
				$(".first-nav>li").removeClass("active");
			    $(".first-nav>li").eq(6).addClass("active"); 
			}
    		
    		if(url == "/employment_practice" || url.indexOf("news_page4") >= 0) {
				$(".first-nav>li").removeClass("active");
			    $(".first-nav>li").eq(8).addClass("active"); 
			}
	    },
	    hover(i){
	    	$(".first-nav>li").eq(i).find('ul').addClass('hover');
	    },
	    leave(i){
	    	$(".first-nav>li").eq(i).find('ul').removeClass('hover');
	    },
	    addFavorite(sURL, sTitle){
    	 	try {
	        	window.external.addFavorite(sURL, sTitle);
		    } catch (e) {
	        	try {
            		window.sidebar.addPanel(sTitle, sURL, "");
	        	} catch (e) {
            		alert("加入收藏失败，请使用Ctrl+D进行添加");
	       		}
		    }
	    }
	},
	mounted(){
			
	}
}	
</script>

<style>
	@import 'header.css';
</style>


