import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Sc_info from '@/components/sc_info'
import Contact from '@/components/contact'
import Sc_dynamics from '@/components/sc_dynamics'
import Construction from '@/components/construction'
import Employment_practice from '@/components/employment_practice'
import Special_construction from '@/components/special_construction'
import Teacher from '@/components/teacher'
import Special_major_accouting from '@/components/special_major_accouting'
import Special_major_accouting1 from '@/components/special_major_accouting1'
import Special_major_artdesign from '@/components/special_major_artdesign'
import Special_major_artdesign1 from '@/components/special_major_artdesign1'
import Special_major_artdesign2 from '@/components/special_major_artdesign2'
import Special_major_computer from '@/components/special_major_computer'
import Special_major_computer1 from '@/components/special_major_computer1'
import News from '@/components/news_page1'
import News2 from '@/components/news_page2'
import News3 from '@/components/news_page3'
import News4 from '@/components/news_page4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/sc_info',
      component: Sc_info
    },
    {
      path: '/contact',
      component: Contact
    },
    {
      path: '/sc_dynamics',
      component: Sc_dynamics
    },
    {
      path: '/construction',
      component: Construction
    },
    {
      path: '/employment_practice',
      component: Employment_practice
    },
    {
      path: '/special_construction',
      component: Special_construction
    },
    {
      path: '/special_major_accouting',
      component: Special_major_accouting
    },
    {
      path: '/special_major_accouting1',
      component: Special_major_accouting1
    },
    {
      path: '/special_major_artdesign',
      component: Special_major_artdesign
    },
    {
      path: '/special_major_artdesign1',
      component: Special_major_artdesign1
    },
    {
      path: '/special_major_artdesign2',
      component: Special_major_artdesign2
    },
    {
      path: '/special_major_computer',
      component: Special_major_computer
    },
    {
      path: '/special_major_computer1',
      component: Special_major_computer1
    },
    {
      path: '/teacher',
      component: Teacher
    },
    {	
    	path: '/news_page1/:id',
    	component: News,
    	name:'news'
    },
    {	
    	path: '/news_page2/:id',
    	component: News2,
    	name:'news2'
    },
    {
    	path: '/news_page3/:id',
    	component: News3,
    	name:'news3'
    },
    {
    	path: '/news_page4/:id',
    	component: News4,
    	name:'news4'
    },
  ]
})



